import Vue from 'vue'
import App from './App.vue'
import TDesign from 'tdesign-vue'//引入tdesign-vue组件库
import 'tdesign-vue/es/style/index.css'//引入tdesign-vue组件库样式
import axios from 'axios'//引入axios
Vue.prototype.$axios = axios//将axios挂载到vue原型上
Vue.use(TDesign)//使用tdesign-vue组件库
Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
