<template>
  <div id="app">
      <t-layout>
        <t-header>
          <span style="font-weight:bold;font-size: 20px;" id="WSTitle"></span>
          <t-radio-group variant="primary-filled" default-value="1">
            <t-radio-button value="1" @click="changeToLight">浅色模式</t-radio-button>
            <t-radio-button value="2" @click="changeToDark">暗黑模式</t-radio-button>
          </t-radio-group>
        </t-header>
        <t-content style="height: 1000px">
          <t-form style="margin: 20px;width: 40%">
            <t-form-item lable="作者检索">
              <t-input v-model="key" placeholder="请输入诗经标题"></t-input>
              <t-button theme="primary" @click="getShijing" @keyup.enter.native="getShijing">检索《诗经》</t-button>
<!--              <t-button theme="primary" >检索《论语》</t-button>-->
            </t-form-item>
            <div id="shijingContent">
              <t-loading size="small" :loading="shijingLoading">
                <t-card :title="shijingTitle" header-bordered footer-bordered style="width: 70vw">
                  <t-skeleton theme="article" v-if="shijingSentencesList.length==0"></t-skeleton>
                  <div id="content" v-for="item in shijingSentencesList" :key="item[0]">
                    {{item}}
                    <br>
                  </div>
                  <br>
                  <template #footer >
                    <t-tag theme="primary" style="margin: 2px;">标题：{{shijingTitle}}</t-tag>
                    <t-tag theme="primary" style="margin: 2px;">篇章：{{shijingChapter}}</t-tag>
                    <t-tag theme="primary" style="margin: 2px;">章节：{{shijingSection}}</t-tag>
                  </template>
                </t-card>
              </t-loading>
            </div>
          </t-form>
          <t-divider/>
          <!--随机三句诗词-->
          <div id="randomSentencesTitle">
            <t-form>
              <t-form-item label="随机三句诗" name="randomThreeSentences">
                <t-button theme="primary" @click="getSentences">换一批/重新加载</t-button>
              </t-form-item>
            </t-form>
            <div id="randomSentencesItems">
              <t-loading size="small" :loading="loading">
                <t-card :title="title1" header-bordered :style="{ width: '30%' }" id="rs1">
                  {{ content1 }}
                  <br><br>
                  <t-tag theme="success" style="margin: 2px;">{{ sid1 }}</t-tag>
                  <t-tag theme="primary" style="margin: 2px;">{{ title1 }}</t-tag>
                </t-card>
              </t-loading>
              <t-loading size="small" :loading="loading">
              <t-card :title="title2" header-bordered :style="{ width: '30%' }" id="rs2">
                {{ content2 }}
                <br><br>
                <t-tag theme="success" style="margin: 2px;">{{ sid2 }}</t-tag>
                <t-tag theme="primary" style="margin: 2px;">{{ title2 }}</t-tag>
              </t-card>
              </t-loading>
              <t-loading size="small" :loading="loading">
                <t-card :title="title3" header-bordered :style="{ width: '30%' }" id="rs3">
                  {{ content3 }}
                  <br><br>
                  <t-tag theme="success" style="margin: 2px;">{{ sid3 }}</t-tag>
                  <t-tag theme="primary" style="margin: 2px;">{{ title3 }}</t-tag>
                </t-card>
              </t-loading>

            </div>

          </div>
        </t-content>
        <t-footer>{{ copyrighgt }} DesignColors 版权所有</t-footer>
      </t-layout>
  </div>
</template>

<script>
// import $ from 'jquery'
export default {
  name: 'App',
  data(){
    return {
      resdata:[],
      title1: '',
      content1: '',
      title2: '',
      content2: '',
      title3: '',
      content3: '',
      loading: false,
      sid1: '',
      sid2: '',
      sid3: '',
      shijingSentencesList:[],
      key: '',
      shijingTitle:'',
      shijingChapter:'',
      shijingSection:'',
      shijingLoading: false,
    }
  },
  components: {
  },
  methods: {
    changeToDark(){
      document.documentElement.setAttribute('theme-mode', 'dark');
    },
    changeToLight(){
      document.documentElement.removeAttribute('theme-mode');
    },
    getSentences(){
      this.loading=true;
      this.$axios.get('/sentence.json',{}).then((res)=>{
        console.log(res.data.list);
        this.resdata = res.data;
        // 3个随机数
        let randomNum1 = Math.floor(Math.random()*9999);
        console.log(randomNum1);
        let randomNum2 = Math.floor(Math.random()*9999);
        console.log(randomNum2);
        let randomNum3 = Math.floor(Math.random()*9999);
        console.log(randomNum3);
        this.title1 = res.data.list[randomNum1].from;
        this.content1 = res.data.list[randomNum1].name;
        this.sid1 = res.data.list[randomNum1].id.oid;
        this.title2 = res.data.list[randomNum2].from;
        this.content2 = res.data.list[randomNum2].name;
        this.sid2 = res.data.list[randomNum2].id.oid;
        this.title3 = res.data.list[randomNum3].from;
        this.content3 = res.data.list[randomNum3].name;
        this.sid3 = res.data.list[randomNum3].id.oid;
        this.loading=false;
      })
    },
    getShijing(){
      this.shijingLoading=true;
      this.$axios.get('/shijing.json',{}).then((res)=>{
        console.log(res.data.list);
        let filt = res.data.list.filter(p =>{
          return (p.title.indexOf(this.key) !== -1);
        })
        this.shijingSentencesList=filt[0].content
        this.shijingTitle = filt[0].title
        this.shijingChapter = filt[0].chapter
        this.shijingSection = filt[0].section
        console.log(this.shijingSentencesList)
        this.shijingLoading=false;
      })

    }
  },
  computed: {
    copyrighgt(){
      let thisYear = new Date().getFullYear();
      let nextYear = thisYear + 1;
      return 'Copyright ©'+thisYear+"-"+nextYear+'DesignColors. All rights reserved.';
    }
  },
  mounted() {
    this.getSentences();

  },
}
</script>

