  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
t-content{
  background-color: #FFFFFF;
}
#WSTitle{
  margin-right: 85vw;
}
#randomSentencesItems{
  float: left;
  width: 100%;
}

#rs1,#rs2,#rs3{
  width: 45%;
  height:200px;
  float: left;
  margin-right: 1%;
  margin-left: 1%;
}

</style>
